.navbar {
    height: 5rem;
    background: rgb(25, 25, 25);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}

.logo {
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}

.desktopMenuListItem {
   margin: 1rem;
   cursor: pointer; 
}
.desktopMenuListItem:hover {
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.desktopMenuBtn {
    background: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 1rem;

}
.desktopMenuImg {
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
}

.active {
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}
.mobMenu {
    display: none; 
    object-fit: cover;
    height: 1.8rem;
    background: white;
    border-radius: 2rem;
}
.navMenu {
    position:absolute;
    align-items: center;
    top: 4rem;
    right: -2ren;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background:rgb(40,40,40);
    border-radius: 1rem;
}

.mobileMenuListItem {
    color: white;
    padding: 0.5rem 3rem;
    margin: 0.5rem;
    border-radius: 1rem;
    background: rgb(40,40,40);
    position: relative;
}

@media screen and (max-width:720px) {
    .mobMenu {
        display: flex;
    }
    .navbar{
        position:sticky;
    }
    .desktopMenu {
        display: none;
    }
    .desktopMenuBtn {
        display: none;
        position:sticky;
    }
}