#works {
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    margin: 0 auto;
    text-align: center;
    padding: 0 1rem;
    
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
}

.worksTitle {
    margin: 1rem 0;
    font-size:  2rem;
}

.worksDesc {
    font-weight: 300;
    font-size: 1rem;
}

.worksImgs {
    
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;
    display: flex;
}

.worksImg {
    object-fit: cover;
    height: 20rem;
    width: 20rem;
    margin: 0.5rem;
    border: 1rem;
    background: #454545
}

.worksBtn {
    margin: 3rem 0 ;
    padding:  0.5rem 2.5rem;
    border: none;
    background: white;
    border-radius: 2rem;
    font-size: 1rem;
}

@media screen and (max-width:680px) {
    .worksImg {
        height: 45vw;
        width: 40vw;
    }
    
}